import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { TableModule } from 'primeng/table';
import { PaginatorModule } from 'primeng/paginator';
import { CalendarModule } from 'primeng/calendar';
import { TreeModule } from 'primeng/tree';
import { SidebarModule } from 'primeng/sidebar';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { PreloadAllModules, RouterModule, Routes} from '@angular/router';
import { AppComponent } from './app.component';
import { HomepageComponent } from './homepage/homepage.component';
import { CommonModule } from '@angular/common';
import { LoginComponent } from './login/login.component';
import { RichiesteComponent } from './richieste/richieste.component';
import { RichiestaDetailComponent } from './richiesta-detail/richiesta-detail.component';
import { DocumentazioneComponent } from './documentazione/documentazione.component';
import {DropdownModule} from 'primeng/dropdown';
import {FileUploadModule} from 'primeng/fileupload';
import {SelectButtonModule} from 'primeng/selectbutton';
import { CommunityComponent } from './community/community.component';
import { FerieComponent } from './ferie/ferie.component';
import { ApplicativiComponent } from './applicativi/applicativi.component';
import { NewsDetailComponent } from './news-detail/news-detail.component';

export const routing: Routes = [
  {path: '', redirectTo: 'login', pathMatch: 'full'},
  {path: 'login', component: LoginComponent},
  {path: 'homepage', component: HomepageComponent},
  {path: 'richieste', component: RichiesteComponent},
  {path: 'applicativi', component: ApplicativiComponent},
  {path: 'community', component: CommunityComponent},
  {path: 'ferie', component: FerieComponent},

  {path: 'richiesta-detail/:id', component: RichiestaDetailComponent},
  {path: 'documentazione', component: DocumentazioneComponent}

];

@NgModule({
  declarations: [
    AppComponent,
    HomepageComponent,
    LoginComponent,
    RichiesteComponent,
    RichiestaDetailComponent,
    DocumentazioneComponent,
    CommunityComponent,
    FerieComponent,
    ApplicativiComponent,
    NewsDetailComponent
  ],
  imports: [
    BrowserModule, 
    TableModule,
    PaginatorModule,
    CalendarModule,
    BrowserAnimationsModule,
    RouterModule.forRoot(routing, {useHash: true, preloadingStrategy: PreloadAllModules}),
    CommonModule,
    TreeModule,
    SidebarModule,
    DropdownModule,
    FileUploadModule,
    SelectButtonModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})

export class AppModule { 
  

  
  
  
}
